.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.form {
    margin-bottom: 20px;
}

.label {
    display: block;
    margin-bottom: 5px;
}

.input {
    width: 100%;
    padding: 8px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.btn {
    display: block;
    width: 100%;
    padding: 8px 20px;
    background-color: #007bff;
    color: #fff;
    margin-top: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.booksList {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.bookItem {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
}

.bookImage {
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.bookItem h3 {
    margin-top: 0;
}

.bookItem p {
    margin: 5px 0;
}